* {
  font-family: "Poppins", sans-serif;
}

body {
  background: #fffefef3;
  color: #343a40;
  transition: background 0.3s ease-in-out, color 0.6s ease-in-out;
}

body.light {
  background-color: #fffefef3;
  color: #343a40;
}

body.dark {
  background-color: #343a40;
  color: #fffefef3;
}

.navtext.light {
  background-color: #fffefef3;
  color: #343a40;
}

.navtext.dark {
  background-color: #343a40;
  color: #fffefef3;
}

