} */

.content-container {
  margin-top: 25px;
  margin-bottom: 50px;
}

.img-landing {
  width: px !important;
}

.footer-container {
  background-color: #f0f0f0 !important;
}

a {
  color: #343a40 !important;
}
a:hover {
  color: #007bff !important;
  text-decoration: none !important;
}

.footer-1 {
  padding: 2vw;
  display: -webkit-flex;
  display: flex;
  color: #343a40 !important;
}

.footer-2 {
  color: #343a40 !important;
}

.navname {
  cursor: pointer;
}

.navname img {
  width: 100px;
}

@media (max-width: 992px) {
  .container-fluid {
    padding: 3vw !important;
  }
}

@media (max-width: 768px) {
  .footer-container {
    text-align: center;
  }

  .landing-text {
    font-size: 1rem;
  }

  .unique {
    margin-bottom: 2em;
  }
}

#wrapper {
  overflow-x: hidden;
}

#sidebar-wrapper {
  min-height: 80vh;
  margin-left: -15rem;
  transition: margin 0.25s ease-out;
}

#sidebar-wrapper .sidebar-heading {
  padding: 0.875rem 1.25rem;
  font-size: 1.2rem;
}

#sidebar-wrapper .list-group {
  width: 15rem;
}

#page-content-wrapper {
  margin-left: 2rem;
  /* min-width: 100vw; */
}

body.sb-sidenav-toggled #wrapper #sidebar-wrapper {
  margin-left: 0;
}

@media (min-width: 768px) {
  #sidebar-wrapper {
    margin-left: 0;
  }

  #page-content-wrapper {
    min-width: 0;
    width: 100%;
  }

  body.sb-sidenav-toggled #wrapper #sidebar-wrapper {
    margin-left: -15rem;
  }
}

#darkMode {
  background: transparent url(/static/media/moon.08c8560a.png) no-repeat center;
  background-size: 30px 30px;
  width: 45px;
  height: 45px;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  border: none;
  border-radius: 50%;
  transition: background-color 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
}

#darkMode:hover,
#darkMode:focus {
  -webkit-filter: none;
          filter: none;
  background-color: #343a40;
  cursor: pointer;
}

#darkMode.clicked {
  -webkit-filter: none !important;
          filter: none !important;
  background-color: #343a40;
}

